.heroSection {
  position: relative;
  min-height: 70vh;
  max-height: 750px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 4rem 1rem; /* Increased padding */
  color: var(--neutral-white);
  overflow: hidden;

  /* Background Image: Replace with your actual image in /public/images/
     Or use a gradient if you prefer.
  */
  background-image: url('/images/hero-background-affiliate.jpg'); /* Example Path */
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

/* If you don't have a hero image yet, you can use a gradient: */
/*
.heroSection {
  background: linear-gradient(135deg, var(--neutral-black) 0%, var(--dark-gray) 100%);
}
*/

.heroOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.65); /* Adjust opacity for readability */
  z-index: 1;
}

.heroContent {
  position: relative;
  z-index: 2;
  max-width: 850px; /* Slightly wider for more impact */
}

.heroTitle {
  font-size: 3rem; /* Responsive: Adjust with media queries if needed */
  font-weight: 800;
  line-height: 1.2;
  margin-bottom: 1.5rem;
  color: var(--neutral-white);
  text-shadow: 2px 3px 7px rgba(0, 0, 0, 0.6); /* Enhanced shadow */
}

.highlightYellow {
  color: var(--primary-yellow);
  /* Add a subtle glow or animation if desired */
  /* text-shadow: 0 0 10px var(--primary-yellow); */
}

.highlightGreen {
  color: #aaffc3; /* Light, vibrant green for contrast on dark backgrounds */
  /* text-shadow: 0 0 8px #aaffc3; */
}

.heroSubtitle {
  font-size: 1.2rem; /* Responsive */
  line-height: 1.75;
  margin-bottom: 2.8rem;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  opacity: 0.95;
  color: var(--light-gray);
}

.heroActions {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.2rem; /* Increased gap */
}

.heroCtaButton {
  padding: 0.9rem 2.2rem; /* Generous padding */
  font-size: 1.05rem;
  font-weight: 600; /* Ensure font-weight is applied */
  min-width: 220px;
  box-shadow: var(--shadow-md); /* Add shadow to buttons */
  transition: transform 0.2s ease-out, box-shadow 0.2s ease-out; /* Smooth transition for hover */
}
.heroCtaButton:hover {
    transform: translateY(-3px) scale(1.03); /* Lift and slightly scale on hover */
    box-shadow: var(--shadow-lg); /* Increase shadow on hover */
}


.heroCtaOutline {
  background-color: transparent;
  border: 2px solid var(--primary-green);
  color: var(--primary-green);
}
.heroCtaOutline:hover {
  background-color: var(--primary-green);
  color: var(--neutral-white);
  border-color: var(--primary-green);
}
/* If using a very dark background for the hero, the green outline might need to be white or yellow for contrast */
