/* Responsive adjustments for Hero Section */
@media (max-width: 992px) {
  .heroTitle {
    font-size: 2.6rem;
  }
  .heroSubtitle {
    font-size: 1.15rem;
  }
}

@media (max-width: 768px) {
  .heroSection {
    min-height: auto; /* Allow height to be determined by content on mobile */
    padding: 3rem 1rem;
  }
  .heroTitle {
    font-size: 2.2rem;
  }
  .heroSubtitle {
    font-size: 1.05rem;
    margin-bottom: 2.2rem;
  }
  .heroActions {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  .heroCtaButton {
    width: 100%;
    max-width: 320px; /* Limit button width on mobile */
    min-width: auto;
    font-size: 1rem;
    padding: 0.8rem 1.5rem;
  }
}

